import { INCREMENT, DECREMENT, GYNECOLOGIST_PATH, FNDOCRINOLOGIST_PATH, PATIENT_SELECT_CARD, API_INCREMENT } from '../constants'

export function increment() {
  return {
    type: INCREMENT
  }
}

export function decrement() {
  return {
    type: DECREMENT
  }
}

export function apiIncrement() {
  return {
    type: API_INCREMENT
  }
}

export function gynecologistPath() {
  return {
    type: GYNECOLOGIST_PATH
  }
}

export function fndocrinologistPath() {
  return {
    type: FNDOCRINOLOGIST_PATH
  }
}

export function patientSelectCard(id) {
  return {
    type: PATIENT_SELECT_CARD,
    payload: id
  }
}



